body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.tickers {
  list-style-type: none;
  padding: 0;
  display: inline-flex;
}
.cryptocurrency {
  padding: 1rem 2rem 2rem;
  margin: 1rem;
  border-radius: 1rem;
  width: 9.5rem;
  border: solid 1px rgba(0, 0, 0, 0.05);
}

.cryptocurrency p {
  color: rgba(0, 0, 0, 0.75);
}

.cryptocurrency-name {
  font-size: 1.25rem;
  font-weight: 500;
}

.rank1 {
  background-color: #57b1ec;
  border-bottom: solid 4px #0d51d1;
}

.rank2 {
  background-color: #c2c9ff;
  border-bottom: solid 4px #454a75;
}

.rank3 {
  background-color: #66dfc5;
  border-bottom: solid 4px #09694c;
}

.rank4 {
  background-color: #e5a7e7;
  border-bottom: solid 4px #770271;
}

